"use client";

import { useState } from "react";
import { useClipboard } from "@/hooks/useClipboard";
import Card from "../ui/Card";
import Button from "../ui/Button";
import { cn } from "@/lib/utils";

interface SoaxExamplesProps {
  proxySoftwareReady: string;
  isComplete: boolean;
}

type ExampleTab = "curl" | "python" | "nodejs";

export default function SoaxExamples({
  proxySoftwareReady,
  isComplete,
}: SoaxExamplesProps) {
  const [activeTab, setActiveTab] = useState<ExampleTab>("curl");
  const { copied, copy } = useClipboard();

  // Parse the software ready format: username:password@host:port
  const atSplit = proxySoftwareReady.split("@");
  const hostPort = atSplit[1] || "proxy.soax.com:5000";
  const userPass = atSplit[0] || "USERNAME:PASSWORD";
  const host = hostPort.split(":")[0] || "proxy.soax.com";
  const port = hostPort.split(":")[1] || "5000";
  // username contains colons, so split on first colon only for user portion
  const firstColon = userPass.indexOf(":");
  const username = firstColon > -1 ? userPass.substring(0, firstColon) : userPass;
  const password = firstColon > -1 ? userPass.substring(firstColon + 1) : "";

  const curlExample = `# SOAX Proxy - cURL Example
curl -x "http://${proxySoftwareReady}" \\
  "http://checker.soax.com/api/ipinfo"`;

  const pythonExample = `# SOAX Proxy - Python Example
import requests

proxy = {
    "http": "http://${proxySoftwareReady}",
    "https": "http://${proxySoftwareReady}"
}

response = requests.get(
    "http://checker.soax.com/api/ipinfo",
    proxies=proxy
)

print(response.json())`;

  const nodejsExample = `// SOAX Proxy - Node.js Example
const axios = require('axios');

const proxy = {
  protocol: 'http',
  host: '${host}',
  port: ${port},
  auth: {
    username: '${username}',
    password: '${password}'
  }
};

axios.get('http://checker.soax.com/api/ipinfo', { proxy })
  .then(res => console.log(res.data))
  .catch(err => console.error(err));`;

  const examples: Record<ExampleTab, string> = {
    curl: curlExample,
    python: pythonExample,
    nodejs: nodejsExample,
  };

  const handleCopy = async () => {
    await copy(examples[activeTab]);
  };

  return (
    <Card hoverable={false}>
      <h3 className="font-bold text-lg text-text-main mb-2">Usage examples</h3>
      <p className="text-sm text-text-muted mb-4">
        Test your SOAX proxy connection using{" "}
        <code className="text-accent font-semibold">
          checker.soax.com/api/ipinfo
        </code>
      </p>

      {/* Tab Buttons */}
      <div className="flex gap-2 mb-4">
        {(["curl", "python", "nodejs"] as ExampleTab[]).map((tab) => (
          <button
            key={tab}
            onClick={() => setActiveTab(tab)}
            className={cn(
              "px-4 py-2 rounded-md text-sm font-medium transition-all",
              activeTab === tab
                ? "bg-accent text-black shadow-sm"
                : "bg-gray-100 text-text-muted hover:bg-gray-200"
            )}
          >
            {tab === "curl"
              ? "cURL"
              : tab === "python"
                ? "Python"
                : "Node.js"}
          </button>
        ))}
      </div>

      {/* Code Block */}
      <div className="relative">
        <pre className="px-4 py-4 rounded-lg bg-gray-900 text-gray-100 text-sm font-mono overflow-x-auto border border-gray-700">
          <code>{examples[activeTab]}</code>
        </pre>

        <div className="absolute top-2 right-2">
          <Button
            variant="secondary"
            success={copied}
            onClick={handleCopy}
            className="!py-1.5 !px-3 !text-xs bg-gray-700 !text-gray-200 !border-gray-600 hover:!bg-gray-600"
          >
            {copied ? "Copied!" : "Copy"}
          </Button>
        </div>
      </div>

      {!isComplete && (
        <p className="mt-3 text-xs text-text-soft">
          Fill in your credentials above to see personalized examples.
        </p>
      )}
    </Card>
  );
}
