"use client";

import { useState } from "react";
import { useClipboard } from "@/hooks/useClipboard";
import Card from "../ui/Card";
import Button from "../ui/Button";
import { cn } from "@/lib/utils";

interface SoaxProxyOutputProps {
  proxySoftwareReady: string;
  proxyHPUP: string;
  isComplete: boolean;
}

type OutputFormat = "software_ready" | "hpup";

export default function SoaxProxyOutput({
  proxySoftwareReady,
  proxyHPUP,
  isComplete,
}: SoaxProxyOutputProps) {
  const [format, setFormat] = useState<OutputFormat>("software_ready");
  const { copied, error, copy } = useClipboard();

  const displayString =
    format === "software_ready" ? proxySoftwareReady : proxyHPUP;

  const handleCopy = async () => {
    if (!isComplete) return;
    await copy(displayString);
  };

  return (
    <Card hoverable={false}>
      <h3 className="font-semibold mb-2">Resulting proxy string</h3>
      <p className="text-xs text-text-soft mb-4">
        This is the final proxy string you can paste into your tools.
        Toggle between{" "}
        <strong className="text-text-main">USERNAME:PASS@HOST:PORT</strong>{" "}
        and{" "}
        <strong className="text-text-main">HOST:PORT:USER:PASS</strong>{" "}
        formats.
      </p>

      {/* Format Toggle */}
      <div className="flex gap-2 mb-4">
        <button
          onClick={() => setFormat("software_ready")}
          className={cn(
            "px-3 py-1.5 rounded-md text-xs font-medium transition-all border-2",
            format === "software_ready"
              ? "bg-accent/15 text-accent border-accent"
              : "bg-gray-50 text-text-muted border-gray-200 hover:border-gray-300"
          )}
        >
          Software ready
        </button>
        <button
          onClick={() => setFormat("hpup")}
          className={cn(
            "px-3 py-1.5 rounded-md text-xs font-medium transition-all border-2",
            format === "hpup"
              ? "bg-accent/15 text-accent border-accent"
              : "bg-gray-50 text-text-muted border-gray-200 hover:border-gray-300"
          )}
        >
          HOST:PORT:USER:PASS
        </button>
      </div>

      <div className="flex gap-3 items-start">
        <code
          className={cn(
            "flex-1 px-3 py-2.5 rounded-md border font-mono text-sm break-all transition-all duration-200",
            "bg-card-alt/95",
            copied
              ? "border-success shadow-[0_0_0_2px_rgba(34,197,94,0.2)]"
              : "border-border-subtle/50"
          )}
        >
          {displayString}
        </code>
        <Button
          variant="secondary"
          success={copied}
          onClick={handleCopy}
          disabled={!isComplete}
        >
          {copied ? "Copied!" : "Copy"}
        </Button>
      </div>

      {(error || (!isComplete && !copied)) && (
        <p
          className={cn(
            "mt-2 text-xs animate-fade-in-up",
            error ? "text-danger" : "text-text-soft"
          )}
        >
          {error || (isComplete ? "" : "Fill in all fields before copying.")}
        </p>
      )}
      {copied && (
        <p className="mt-2 text-xs text-success animate-fade-in-up">
          Proxy string copied to clipboard.
        </p>
      )}
    </Card>
  );
}
