"use client";

import { useState, useMemo } from "react";
import { useClipboard } from "@/hooks/useClipboard";
import Card from "../ui/Card";
import Button from "../ui/Button";
import { SoaxProxyConfig, SoaxListConfig } from "@/lib/types";
import { downloadTxt } from "@/lib/utils";

interface SoaxProxyListProps {
  config: SoaxProxyConfig;
  listConfig: SoaxListConfig;
  updateListConfig: (updates: Partial<SoaxListConfig>) => void;
  generateList: () => string[];
  isComplete: boolean;
}

export default function SoaxProxyList({
  config,
  listConfig,
  updateListConfig,
  generateList,
  isComplete,
}: SoaxProxyListProps) {
  const [proxyLines, setProxyLines] = useState<string[]>([]);
  const [status, setStatus] = useState<{
    message: string;
    type: "success" | "error";
  } | null>(null);
  const { copied, copy } = useClipboard();

  const handleGenerate = () => {
    const lines = generateList();
    if (lines.length === 0) {
      setStatus({
        message: "Unable to generate proxies. Please try again.",
        type: "error",
      });
      return;
    }
    setProxyLines(lines);
    setStatus({
      message: `Generated ${lines.length} proxies with ${config.sessionType} sessions.`,
      type: "success",
    });
  };

  const handleCopy = async () => {
    if (!proxyLines.length) {
      setStatus({ message: "Generate a list first.", type: "error" });
      return;
    }
    const success = await copy(proxyLines.join("\n"));
    if (success) {
      setStatus({
        message: "Proxy list copied to clipboard.",
        type: "success",
      });
    }
  };

  const handleDownload = () => {
    if (!proxyLines.length) {
      setStatus({ message: "Generate a list first.", type: "error" });
      return;
    }
    downloadTxt(proxyLines.join("\n"), "soax-proxies.txt");
    setStatus({ message: "Download started.", type: "success" });
  };

  const parsedRows = useMemo(() => {
    return proxyLines
      .map((line) => {
        // Software ready format: user:pass@host:port
        if (line.includes("@")) {
          const [userPass, hostPort] = line.split("@");
          const firstColon = userPass.indexOf(":");
          const user =
            firstColon > -1 ? userPass.substring(0, firstColon) : userPass;
          const pass =
            firstColon > -1 ? userPass.substring(firstColon + 1) : "";
          const host = hostPort?.split(":")[0] || "";
          const port = hostPort?.split(":")[1] || "";
          return { host, port, user, pass };
        }
        // HOST:PORT:USER:PASS format
        const parts = line.split(":");
        if (parts.length < 4) return null;
        return {
          host: parts[0],
          port: parts[1],
          user: parts[2],
          pass: parts.slice(3).join(":"),
        };
      })
      .filter(Boolean);
  }, [proxyLines]);

  return (
    <Card hoverable={false}>
      <h3 className="font-bold text-lg text-text-main mb-2">
        Proxy list generator
      </h3>
      <p className="text-sm text-text-muted mb-5">
        Create multiple SOAX proxy lines from the current configuration and view
        them separated as Host / Port / User / Pass.
      </p>

      <div className="grid sm:grid-cols-2 gap-4 mb-4">
        <div>
          <label className="block mb-1.5 text-sm font-medium text-text-main">
            Number of connections
          </label>
          <input
            type="number"
            min={1}
            max={1000}
            value={listConfig.quantity}
            onChange={(e) =>
              updateListConfig({ quantity: parseInt(e.target.value) || 1 })
            }
            className="w-full px-3 py-2.5 rounded-md bg-gray-50 text-text-main text-sm border-2 border-gray-200 outline-none transition-all focus:border-accent focus:bg-white"
          />
        </div>

        <div>
          <label className="block mb-1.5 text-sm font-medium text-text-main">
            Output format
          </label>
          <select
            value={listConfig.outputMask}
            onChange={(e) =>
              updateListConfig({ outputMask: e.target.value })
            }
            className="w-full px-3 py-2.5 rounded-md bg-gray-50 text-text-main text-sm border-2 border-gray-200 outline-none cursor-pointer focus:border-accent focus:bg-white"
          >
            <option value="software_ready">
              Software ready (user:pass@host:port)
            </option>
            <option value="host_port_user_pass">
              HOST:PORT:USER:PASS
            </option>
          </select>
          <p className="mt-1.5 text-xs text-text-muted">
            Each proxy gets a unique <code className="text-accent font-semibold">_x{"{N}"}</code> connection
            number.
          </p>
        </div>
      </div>

      {/* Advanced Options */}
      <div className="grid sm:grid-cols-2 gap-4 mb-4 p-4 rounded-lg bg-gray-50 border border-gray-200">
        <div>
          <label className="block mb-2 text-sm font-medium text-text-main">
            Replace space symbol with:
          </label>
          <div className="flex gap-4">
            {[
              { value: "+", label: "+" },
              { value: "_", label: "_" },
              { value: "%20", label: "%20" },
            ].map((opt) => (
              <label
                key={opt.value}
                className="flex items-center gap-1.5 text-sm text-text-main cursor-pointer"
              >
                <input
                  type="radio"
                  name="spaceReplace"
                  value={opt.value}
                  checked={listConfig.spaceReplace === opt.value}
                  onChange={(e) =>
                    updateListConfig({ spaceReplace: e.target.value })
                  }
                  className="w-4 h-4 accent-accent"
                />
                <span className="font-mono">{opt.label}</span>
              </label>
            ))}
          </div>
        </div>

        <div>
          <label className="block mb-2 text-sm font-medium text-text-main">
            Replace &quot;&amp;&quot; symbol with:
          </label>
          <div className="flex gap-4">
            {[
              { value: "", label: "don't replace" },
              { value: "%26", label: "%26" },
            ].map((opt) => (
              <label
                key={opt.value || "none"}
                className="flex items-center gap-1.5 text-sm text-text-main cursor-pointer"
              >
                <input
                  type="radio"
                  name="ampReplace"
                  value={opt.value}
                  checked={listConfig.ampReplace === opt.value}
                  onChange={(e) =>
                    updateListConfig({ ampReplace: e.target.value })
                  }
                  className="w-4 h-4 accent-accent"
                />
                <span>{opt.label}</span>
              </label>
            ))}
          </div>
        </div>
      </div>

      <div className="mb-4">
        <label className="block mb-1.5 text-sm font-medium text-text-main">
          Formatted proxy list
        </label>
        <textarea
          readOnly
          rows={6}
          value={proxyLines.join("\n")}
          placeholder="Click 'Generate list' to create SOAX proxies..."
          className="w-full px-3 py-2.5 rounded-md bg-gray-50 text-text-main text-sm font-mono border-2 border-gray-200 outline-none resize-y focus:border-accent"
        />

        <div className="flex flex-wrap gap-3 mt-4">
          <Button variant="secondary" onClick={handleGenerate}>
            Generate list
          </Button>
          <Button variant="secondary" success={copied} onClick={handleCopy}>
            {copied ? "Copied!" : "Copy list"}
          </Button>
          <Button variant="secondary" onClick={handleDownload}>
            Download .txt
          </Button>
        </div>

        {status && (
          <p
            className={`mt-3 text-sm font-medium animate-fade-in-up ${
              status.type === "error" ? "text-danger" : "text-success"
            }`}
          >
            {status.message}
          </p>
        )}
      </div>

      {/* Proxy Table */}
      {parsedRows.length > 0 && (
        <div className="mt-6">
          <label className="block mb-2 text-sm font-medium text-text-main">
            Separated view (Host / Port / User / Pass)
          </label>
          <div className="rounded-lg border border-gray-200 bg-white max-h-64 overflow-auto shadow-subtle">
            <table className="w-full text-sm">
              <thead>
                <tr className="bg-gray-100 sticky top-0 z-10">
                  <th className="px-4 py-2.5 text-left border-b border-gray-200 font-semibold text-text-main">
                    Host
                  </th>
                  <th className="px-4 py-2.5 text-left border-b border-gray-200 font-semibold text-text-main">
                    Port
                  </th>
                  <th className="px-4 py-2.5 text-left border-b border-gray-200 font-semibold text-text-main">
                    User
                  </th>
                  <th className="px-4 py-2.5 text-left border-b border-gray-200 font-semibold text-text-main">
                    Pass
                  </th>
                </tr>
              </thead>
              <tbody>
                {parsedRows.map((row, i) => (
                  <tr
                    key={i}
                    className={`transition-colors hover:bg-accent/10 ${
                      i % 2 === 0 ? "bg-white" : "bg-gray-50"
                    }`}
                  >
                    <td className="px-4 py-2 border-b border-gray-100 whitespace-nowrap text-text-main">
                      {row?.host}
                    </td>
                    <td className="px-4 py-2 border-b border-gray-100 whitespace-nowrap text-text-main">
                      {row?.port}
                    </td>
                    <td className="px-4 py-2 border-b border-gray-100 whitespace-nowrap text-text-main font-mono text-xs">
                      {row?.user}
                    </td>
                    <td className="px-4 py-2 border-b border-gray-100 whitespace-nowrap text-text-muted font-mono text-xs">
                      {row?.pass}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      )}
    </Card>
  );
}
