"use client";

import { useState, useCallback, useMemo } from "react";
import { SoaxProxyConfig, SoaxListConfig } from "@/lib/types";
import {
  buildSoaxSoftwareReady,
  buildSoaxProxyString,
  buildSoaxUsername,
  generateSoaxProxyList,
  randomSoaxSessionName,
} from "@/lib/utils";

// Fixed SOAX credentials
const SOAX_CREDENTIALS = {
  packageId: "291428",
  password: "YrwZi4ZXpbypprwa",
  host: "proxy.soax.com",
  port: "5000",
};

interface UseSoaxProxyReturn {
  config: SoaxProxyConfig;
  listConfig: SoaxListConfig;
  proxySoftwareReady: string;
  proxyHPUP: string;
  usernamePreview: string;
  updateConfig: (updates: Partial<SoaxProxyConfig>) => void;
  updateListConfig: (updates: Partial<SoaxListConfig>) => void;
  generateList: () => string[];
  regenerateSession: () => void;
  isComplete: boolean;
}

const initialConfig: SoaxProxyConfig = {
  packageId: SOAX_CREDENTIALS.packageId,
  password: SOAX_CREDENTIALS.password,
  host: SOAX_CREDENTIALS.host,
  port: SOAX_CREDENTIALS.port,
  proxyType: "residential",
  location: "",
  usePreciseTargeting: false,
  region: "",
  city: "",
  isp: "",
  sessionType: "sticky",
  sessionPeriod: "3600",
  sessionName: randomSoaxSessionName(),
  bindIp: false,
  behaviourModifier: "disabled",
};

const initialListConfig: SoaxListConfig = {
  quantity: 1,
  outputMask: "software_ready",
  spaceReplace: "+",
  ampReplace: "",
};

export function useSoaxProxy(): UseSoaxProxyReturn {
  const [config, setConfig] = useState<SoaxProxyConfig>(initialConfig);
  const [listConfig, setListConfig] = useState<SoaxListConfig>(initialListConfig);

  const updateConfig = useCallback((updates: Partial<SoaxProxyConfig>) => {
    setConfig((prev) => {
      const next = { ...prev, ...updates };
      // Reset precise targeting fields when toggling off
      if (updates.usePreciseTargeting === false) {
        next.region = "";
        next.city = "";
        next.isp = "";
      }
      return next;
    });
  }, []);

  const updateListConfig = useCallback((updates: Partial<SoaxListConfig>) => {
    setListConfig((prev) => ({ ...prev, ...updates }));
  }, []);

  const regenerateSession = useCallback(() => {
    setConfig((prev) => ({ ...prev, sessionName: randomSoaxSessionName() }));
  }, []);

  const proxySoftwareReady = useMemo(
    () => buildSoaxSoftwareReady(config),
    [config]
  );

  const proxyHPUP = useMemo(
    () => buildSoaxProxyString(config),
    [config]
  );

  const usernamePreview = useMemo(
    () => buildSoaxUsername(config),
    [config]
  );

  const isComplete = useMemo(() => {
    return !!(config.packageId && config.password && config.host && config.port);
  }, [config]);

  const generateList = useCallback(() => {
    return generateSoaxProxyList(config, listConfig);
  }, [config, listConfig]);

  return {
    config,
    listConfig,
    proxySoftwareReady,
    proxyHPUP,
    usernamePreview,
    updateConfig,
    updateListConfig,
    generateList,
    regenerateSession,
    isComplete,
  };
}
